import axios from "axios";
import React from "react";
import { IoIosArrowDropdown } from "react-icons/io";
import { useSelector, useDispatch } from "react-redux";
import { API_END_POINT } from "../utils/constant";
import { setUser } from "../redux/userSlice";
import { useNavigate } from "react-router-dom";
import toast from "react-hot-toast";
import { setToggle } from "../redux/movieSlice";

const Header = () => {
  const user = useSelector((store) => store.app.user);
  const toggle = useSelector((store) => store.movie.toggle);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const searchClick = () => {
    dispatch(setToggle());
  };

  const logoutHandler = async () => {
    try {
      const user = await axios.get(`${API_END_POINT}/logout`);
      if (user.data.success) {
        toast.success(user.data.message);
      }
      dispatch(setUser(null));
      navigate("/");
    } catch (error) {
      console.log(error);
    }
  };

  return (
    <div className="absolute z-10 flex w-[100%] items-center px-6 justify-between bg-gradient-to-b from-black">
      <img
        className="w-56"
        src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/2560px-Netflix_2015_logo.svg.png"
        alt="netflix_img"
      ></img>

      {user && (
        <div className="flex items-center">
          <IoIosArrowDropdown size="24px" color="white" />
          <h1 className="text-center text-white ml-2 text-lg font-medium">
            {user.fullName}
          </h1>
          <div>
            <button
              onClick={logoutHandler}
              className="bg-red-800 text-white px-4 py-2 ml-2"
            >
              Logout
            </button>
            <button
              onClick={searchClick}
              className="bg-red-800 text-white px-4 py-2 ml-2"
            >
              {toggle ? "Home" : "Search movie"}
            </button>
          </div>
        </div>
      )}
    </div>
  );
};

export default Header;
